import React from 'react';
import './Form.css';

const Form = ()=>{
    return(
        <div className='Form'>
            <form action="https://formspree.io/fernando.n.oviedo@gmail.com" method="POST" encType="multipart/form-data">
                <div>
                    <input type="text" name="name" placeholder=" Name" className='Name'/>
                </div>

                <div>
                    <input type="email" name="_replyto" placeholder=" Your email" className='Email'/>
                </div>

                <div className='Send'>
                    <textarea name="message" placeholder=" Your message :)"></textarea>
                    <input type="submit" value="↑"/>
                </div>

                <input type="hidden" name="_confirmation" value=""/>
                <input type="hidden" name="_subject" value="Contact form submitted"/>
                <input type="hidden" name="_after" value="https://myhomepage.net/"/>
                <input type="hidden" name="_honeypot" value=""/>
            </form>
               
        </div>
    )
}

export default Form;