import React, {Component} from 'react';
import './Footer.css';

import Form from './Form/Form';

class Footer extends Component{

    render(){
        return(
            <div className='Footer' id='Footer'>
                <div className='Contact-form'>
                    <Form/>
                </div>
                <div className='foot'>
                    <div className='up-btn'>
                        <a href="#Home"><img alt='arrow' src='./img/arrow-up.png'></img></a>
                    </div>
                    <div className='social'>
                        <div><a href='#'><img alt='social' src='img/social1.png'></img></a></div>
                        <div><a href='#'><img alt='social' src='img/social2.png'></img></a></div>
                        <div><a href='#'><img alt='social' src='img/social3.png'></img></a></div>
                    </div>
                    <h3>Fernando Oviedo <span>2019</span></h3>
                </div>
            </div>
        )
    }
}

export default Footer;